import {profileAPI, userAPI} from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
    posts: [
        {id: 1, message: 'hi'},
        {id: 2, message: 'ho'}
    ],
    profile: null,
    status: '',
};

const ProfileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST:
            return {
                ...state,//  stateCopy      state ()
                newPostText: '',//
                posts: [...state.posts, {id: 3, message: action.newPostText}]//        ...       
            }

        case SET_STATUS:
            return {
                ...state,
                status: action.status,
            }
        case SET_USER_PROFILE:
            return {...state, profile: action.profile}

        case DELETE_POST:
            return {...state, posts: state.posts.filter(p => p.id != action.postId)}
    }
    return state;
}

export const addPostActionCreator = (newPostText) => {
    return {
        type: ADD_POST, newPostText
    }
}
export const setUsersProfile = (profile) => {
    return {
        type: SET_USER_PROFILE, profile
    }
}
export const setStatus = (status) => {
    return {
        type: SET_STATUS, status
    }
}
export const deletePost = (postId) => {
    return {
        type: DELETE_POST, postId
    }
}

export const getUsersProfile = (userId) => (diapatch) => {
    userAPI.getProfile(userId).then(response => {
        diapatch(setUsersProfile(response.data));
    });
}


export const getStatus = (userId) => (diapatch) => {
    profileAPI.getStatus(userId).then(response => {
        diapatch(setStatus(response.data));
    });
}

export const updateStatus = (status) => (diapatch) => {
    profileAPI.updateStatus(status) //    ,      0      
        .then(response => {
            if (response.data.resultCode === 0) {
                diapatch(setStatus(status));
            }
        });
}

export default ProfileReducer;