import axios from "axios";

const instance = axios.create({ //    instance   axios.create ( instance   axios)
    withCredentials: true, //  withCredentials       
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "7300fadf-18dd-428e-9d99-dcbb08c65b81"
    }
});

export const userAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`, {})
            .then(response => { //  response     response  data
                return response.data;
            });
    },
    follow(userId) {
        return instance.post(`follow/${userId}`)
    },
    unfollow(userId) {
        return instance.delete(`follow/${userId}`)
    },
    getProfile(userId) {
        console.warn('obsolete method, please profileAPI obj ')
        return profileAPI.getProfile(userId);
    }

}
export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId);
    },
    getStatus(userId) {
        return instance.get(`profile/status/` + userId);
    },
    updateStatus(status) {
        return instance.put(`profile/status`, {status: status});
    },
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`, );
    },
    login(email, password, rememderMe = false) { //         
        return instance.post(`/auth/login`, {email, password, rememderMe} );
    },
    logout() { // delete   
        return instance.delete(`/auth/login`);
    }
}



