import React from 'react';
import classes from './Navbar.module.css'
import {NavLink} from "react-router-dom";

const Navbar = () => {
    return (
        <nav className={classes.nav}>
            <div className={classes.item}>
                <NavLink to='/profile' className = { navData => navData.isActive ? classes.active : classes.item }>3</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to='/secondPost' className = { navData => navData.isActive ? classes.active : classes.item }>4</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to='/Car' className = { navData => navData.isActive ? classes.active : classes.item }>5</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to='/users' className = { navData => navData.isActive ? classes.active : classes.item }>6</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to='/login' className = { navData => navData.isActive ? classes.active : classes.item }>7</NavLink>
            </div>
            <div className={classes.item}>
                <NavLink to='/dialogs' className = { navData => navData.isActive ? classes.active : classes.item }>8</NavLink>
            </div>
        </nav>
    );
};

export default Navbar;