import React from 'react';
import {addPostCarCreator, } from "../../redux/car-reducer";
import Car from "./Car";
import {connect} from "react-redux";
import {compose} from "redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";

let mapStateToProps = (state) => {

    return{
        messagesCar: state.profileCar.messagesCar,
        newPostCar: state.profileCar.newPostCar
    }
}

let mapDispatchToProps = (dispatch) => {
    return{
        addPost: (newPostCar) => {
            dispatch(addPostCarCreator(newPostCar));

        }
    }
}

/*const CarContainer = connect (mapStateToProps, mapDispatchToProps)(Car);

export default CarContainer;*/


export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    withAuthRedirect
)(Car);
